import React, { FunctionComponent } from "react";
import styled from "styled-components";
import { AnimateCSS, IAnimateCSS, uc } from "../AnimateCSS"

interface Props extends IAnimateCSS {
  /**
   * Animate mode "in"|"out"
   * 
   * @default "in"
   */
  mode?: string;
}

const LightSpeedStyled = styled(AnimateCSS) <Props>`
  
  @keyframes lightSpeedIn {
    from {
      transform: translate3d(100%, 0, 0) skewX(-30deg);
      opacity: 0;
    }

    60% {
      transform: skewX(20deg);
      opacity: 1;
    }

    80% {
      transform: skewX(-5deg);
    }

    to {
      transform: translate3d(0, 0, 0);
    }
  }

  @keyframes lightSpeedOut {
    from {
      opacity: 1;
    }

    to {
      transform: translate3d(100%, 0, 0) skewX(30deg);
      opacity: 0;
    }
}

  animation-name: ${p => p.animate ? "lightSpeed" + uc(p.mode) : "none"};
  animation-timing-function: ${p => "ease" + p.mode};
`;

const LightSpeed: FunctionComponent<Props> = props => {

  const { children, mode = "in", ...others } = props;

  return (
    <LightSpeedStyled mode={mode} {...others}>
      {children}
    </LightSpeedStyled>
  );
}

export default LightSpeed;
try {
    // @ts-ignore
    LightSpeed.displayName = "LightSpeed";
    // @ts-ignore
    LightSpeed.__docgenInfo = { "description": "", "displayName": "LightSpeed", "props": { "mode": { "defaultValue": { value: "\"in\"" }, "description": "Animate mode \"in\"|\"out\"", "name": "mode", "required": false, "type": { "name": "string" } } } };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["src/components/LightSpeed/index.tsx#LightSpeed"] = { docgenInfo: LightSpeed.__docgenInfo, name: "LightSpeed", path: "src/components/LightSpeed/index.tsx#LightSpeed" };
}
catch (__react_docgen_typescript_loader_error) { }